@use "../../global" as g;
.news{
    &_main{
        padding: g.vw(48,320) 0 0;
        @include g.mq('md') {
            padding: g.vw(48) 0 0;
        }
        @include g.mq('xxl') {
            padding: 48px 0 0;
        }
        .container{
            display: flex;
            flex-wrap: wrap;
            justify-content: space-between;
            @include g.mq('md') {
                flex-wrap: nowrap;
            }
        }
    }
    &_sidebar{
        width: 100%;
        @include g.mq('md') {
            width: g.vw(260);
        }
        @include g.mq('xxl') {
            width: 260px;
        }
    }
    &_archive{
        border: solid 1px #E6EBEF;
        padding: g.vw(25,320) g.vw(15,320);
        @include g.mq('md') {
            padding: g.vw(32) g.vw(29) g.vw(39);
        }
        @include g.mq('xxl') {
            padding: 32px 29px 39px;
        }
        &_date{
            li{
                line-height: 1.5em;
                margin-bottom: g.vw(5,320);
                @include g.mq('md'){
                    margin-bottom: g.vw(9);
                }
                @include g.mq('xxl') {
                    margin-bottom: 9px;
                }
                &:last-child{
                    margin-bottom: 0;
                }
            }
            a{
                display: inline-block;
                @include g.fonts(g.$f_mosarat, 16, bold, 35, 0, #44474E);
                font-size: g.rem(14);
                line-height: 1.5em;
                @include g.mq('sm') {
                    font-size: g.vw(14,376);
                }
                @include g.mq('md'){
                    font-size: g.vw(16);
                    transition: all 0.5s;
                    &:hover{
                        cursor: pointer;
                        color: #00ADC6;
                    }                    
                }
                @include g.mq('xxl') {
                    font-size: g.rem(16);
                }
            }
        }
    }
    &_ttl{
        margin-bottom: g.vw(20,320);
        @include g.mq('md'){
            margin-bottom: g.vw(36);
        }
        @include g.mq('xxl') {
            margin-bottom: 36px;
        }
        .ttl_en{
            display: block;
            @include g.fonts(g.$f_mosarat, 16, 400, 32, 30, #00ADC6);
            font-size: g.rem(16);
            line-height: 1.5em;
            @include g.mq('sm') {
                font-size: g.vw(16,375);
            }
            @include g.mq('md'){
                font-size: g.vw(16);
            }
            @include g.mq('xxl') {
                font-size: g.rem(16);
            }
        }
        .ttl_jp{
            display: block;
            @include g.fonts(0, 20, bold, 35, 0, #44474E); 
            font-size: g.rem(20);
            line-height: 1.3em;
            @include g.mq('sm') {
                font-size: g.vw(20,376);
            }
            @include g.mq('md'){
                font-size: g.vw(20);
            }
            @include g.mq('xxl') {
                font-size: g.rem(20);
            }
        }
    }
    &_content{
        width: 100%;
        margin: 0 0 g.vw(30,375) 0;
        @include g.mq('md') {
            width: g.vw(860);
            margin: 0 g.vw(40) 0 0;
        }
        @include g.mq('xxl') {
            width: 860px;
            margin: 0 40px 0 0;
        }
    }
    &_filter{
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        width: 100%;
        border: solid 1px #E6EBEF;
        padding: g.vw(30,375) g.vw(15,375);
        margin-bottom: g.vw(20,375);
        @include g.mq('md') {
            flex-wrap: nowrap;
            padding: g.vw(36) g.vw(25) g.vw(14) g.vw(50);
            margin-bottom: g.vw(50);
        }
        @include g.mq('xxl') {
            padding: 36px 25px 14px 50px;
            margin-bottom: 50px;
        }
        .news_ttl{
            flex-shrink: 0;
            position: relative;
            margin: 0 0 g.vw(25,375) 0;
            padding: 0 0 g.vw(10,375) 0;
            top: 0;
            width: 100%;
        
            @include g.mq('md') {
                width: auto;
                border: 0;
                top: g.vw(-12);
                margin: 0;
                padding:0 g.vw(30) 0 0;
            }
            @include g.mq('xxl') {
                top: -12px;
                margin: 0;
                padding:0 30px 0 0;
            }
            &::after{
                position: absolute;
                content: '';
                left: 0;
                bottom: 0;
                background: #E6EBEF;
                width: g.vw(40,375);
                height: 1px;
                @include g.mq('md') {
                    content:'';
                    right: 0;
                    left: auto;
                    width: 1px;
                    height: g.vw(40);
                    top: 50%;
                    transform: translateY(-50%);
                }                
                @include g.mq('xxl') {
                    height: 40px;
                }
            }
        }
    }
    &_categories{
        display: flex;
        flex-wrap: wrap;
        justify-content: flex-start;
        padding-left: 0;
        @include g.mq('md') {
            padding-left: g.vw(70);
        }
        @include g.mq('xxl') {
            padding-left: 70px;
        }
    }
    &_cate{
        margin: 0 g.vw(6,375) g.vw(10,375) 0;
        width: auto;
        min-width: g.vw(63,375);
        @include g.mq('md') {
            min-width: g.vw(72);
            margin: 0 g.vw(20) g.vw(20) 0;
        }
        @include g.mq('xxl') {
            min-width: 72px;
            margin: 0 20px 20px 0;
        }
        .link{
            text-decoration:none;
            background: #F5FAFB;
            padding: g.vw(8,375) g.vw(16,375);
            color: #44474E;
            font-weight: bold;
            line-height: 1.5em;
            display: flex;
            align-items: center;
            justify-content: center;
            text-align: center;
            border-radius: 10vw;
            width: 100%;
            height: 100%;
            @include g.mq('md') {
                border-radius: 20px;
                padding: g.vw(8) g.vw(16);
                transition: all 0.5s;
                &:hover{
                    background: #00ADC6;
                    color: #fff;
                }
            }
            @include g.mq('xxl') {
                padding: 8px 16px;
            }
        }
        &.active{
            .link{
                background: #00ADC6;
                color: #fff;
            }
        }
    }
    &_post{
        position: relative;
        display: flex;
        flex-wrap: wrap;
        flex-direction: column;
        text-decoration: none;
        background: #F6FAFB;
        margin-bottom: g.vw(10,375);
        padding: g.vw(30,375) g.vw(40,375) g.vw(30,375) g.vw(20,375);
        @include g.mq('md') {
            margin-bottom: g.vw(10);
            padding: g.vw(40) g.vw(70) g.vw(33) g.vw(50);
            transition: all 0.5s;
            &:hover{
                background: #E6F0F7;
                &::after{
                    @include g.mq('md') {
                        right: g.vw(40);
                    }
                    @include g.mq('xxl') {
                        right: 40px;
                    }
                }
            }
        }
        @include g.mq('xxl') {
            margin-bottom: 10px;
            padding: 40px 70px 33px 50px;
        }
        &::after{
            position: absolute;
            content: '';
            top: 50%;
            right: g.vw(10,375);
            transform: translateY(-50%);
            width: g.vw(10,375);
            height: g.vw(10,375);
            background: url(../images/news/icon_arrow01.svg) no-repeat center/contain;
            @include g.mq('md') {
                transition: all 0.5s;
                width: g.vw(12);
                height: g.vw(12);
                right: g.vw(44);
            }
            @include g.mq('xxl') {
                width: 12px;
                height: 12px;
                right: 44px;
            }
        }
        &_ttl{
            display: block;
            margin-bottom: g.vw(5,375);
            @include g.fonts(0, 20, bold, 35, 0, #44474E);
            font-size: g.rem(16);
            line-height: 1.5em;
            @include g.mq('sm') {
                font-size: g.vw(16,375);
            }
            @include g.mq('md') {
                margin-bottom: g.vw(4);
                font-size: g.vw(20);
            }
            @include g.mq('xxl') {
                font-size: g.rem(20);
                margin-bottom: 4px;
            }
        }
        &_info{
            .date{
                @include g.fonts(g.$f_mosarat, 16, 500, 30, 30, #9BA6AF);
                line-height: 1.5em;
                font-size: g.rem(14);
                display: inline-block;
                margin: 0 g.vw(20,375) 0 0;
                @include g.mq('sm') {
                    font-size: g.vw(14,375);
                }
                @include g.mq('md') {
                    font-size: g.vw(16);
                    margin: 0 g.vw(18) 0 0;
                }
                @include g.mq('xxl') {
                    font-size: g.rem(16);
                    margin: 0 18px 0 0;
                }
            }
            .cate{
                @include g.fonts(0, 16, bold, 32, 0, #00ADC6);
                line-height: 1.5em;
                font-size: g.rem(14);
                display: inline-block;
                @include g.mq('sm') {
                    font-size: g.vw(14,375);
                }
                @include g.mq('md') {
                    font-size: g.vw(16);
                }
                @include g.mq('xxl') {
                    font-size: g.rem(16);
                }
            }
        }
    }
}
.pagination, .wp-pagenavi{
    margin: g.vw(30,375) auto 0;
    @include g.mq('md') {
        margin: g.vw(40) auto 0;
    }
    @include g.mq('xxl') {
        margin: 40px auto 0;
    }
}



.p_new_detail{
    padding-top: g.vw(70,375);
    @include g.mq('md') {
        padding-top: g.vw(193);
    }
    @include g.mq('xxl') {
        padding-top: 193px;
    }
}
.news_detail{
    padding: g.vw(50) 0 0;
    @include g.mq('md') {
        padding: g.vw(44) 0 0;
    }
    @include g.mq('xxl') {
        padding: 44px 0 0;
    }
    &_content{
        position: relative;
        padding: g.vw(40,375) 0;
        @include g.mq('md') {
            padding: g.vw(94) 0;
        }
        @include g.mq('xxl') {
            padding: 94px 0;
        }
        &::before{
            position: absolute;
            content:'';
            top: 0;
            width: 200vw;
            left:-100vw;
            height: 100%;
            background: #F6FAFB;
            z-index: -1;
        }
        a{
            text-decoration: underline;
            color: #00ADC6;
            @include g.mq('md') {
                transition: all 0.5s;
                &:hover{
                    opacity: 0.8;
                    text-decoration: none;
                }
            }
        }
        img{
            max-height: 500px;
            width: auto;
            height: auto;
        }
        /* Fix Images won’t align in WordPress editor  */
        img.centered {
            display: block;
            margin-left: auto;
            margin-right: auto;
        }
        .aligncenter{
            display: block;
            margin-left: auto;
            margin-right: auto;
        }
        img.alignright {
            padding: 4px;
            margin: 0 0 2px 7px;
            display: inline;
        }
        img.alignleft {
            padding: 4px;
            margin: 0 7px 2px 0;
            display: inline;
        }
        .alignright {
            float: right;
        }
        .alignleft {
            float: left;
        }
    }
    &_ttl{
        font-size: g.rem(26);
        font-weight: bold;
        letter-spacing: 0;
        color: #44474E;
        line-height: 1.5em;
        margin-bottom: g.vw(30);
        @include g.mq('sm') {
            font-size: g.vw(26,375);
        }
        @include g.mq('md') {
            line-height: 1.75em;
            font-size: g.vw(40);
            margin-bottom: g.vw(43);
        }
        @include g.mq('xxl') {
            font-size: g.rem(40);
            margin-bottom: 43px;
        }
    }
    &_info{
        margin: 0 0 g.vw(30,375) 0;
        @include g.mq('md') {
            margin: 0 0 g.vw(50) 0;
        }
        @include g.mq('xxl') {
            margin: 0 0 50px 0;
        }
        .date{
            @include g.fonts(g.$f_mosarat, 20, 500, 30, 30, #9BA6AF);
            line-height: 1.5em;
            font-size: g.rem(18);
            display: inline-block;
            margin: 0 g.vw(15,375) 0 0;
            @include g.mq('sm') {
                font-size: g.vw(18,375);
            }
            @include g.mq('md') {
                font-size: g.vw(20);
                margin: 0 g.vw(28) 0 0;
            }
            @include g.mq('xxl') {
                font-size: g.rem(20);
                margin: 0 28px 0 0;
            }
        }
        .cate{
            @include g.fonts(0, 20, bold, 32, 0, #00ADC6);
            line-height: 1.5em;
            font-size: g.rem(18);
            display: inline-block;
            @include g.mq('sm') {
                font-size: g.vw(18,375);
            }
            @include g.mq('md') {
                font-size: g.vw(20);
            }
            @include g.mq('xxl') {
                font-size: g.rem(20);
            }
        }
    }
    &_content{
        .desc01{
            margin-bottom: g.vw(30,375);
            @include g.mq('md') {
                margin-bottom: g.vw(33);
            }
            @include g.mq('xxl') {
                margin-bottom: 33px;
            }
        }
        .desc02{
            margin-bottom: g.vw(30,375);
            @include g.mq('md') {
                margin-bottom: g.vw(30);
            }
            @include g.mq('xxl') {
                margin-bottom: 30px;
            }
        }
    }
    &_txts{
        margin-bottom: g.vw(30,375);
        padding-left: g.vw(0,375);
        @include g.mq('md') {
            padding-left: g.vw(15);
            margin-bottom: g.vw(30);
        }
        @include g.mq('xxl') {
            padding-left: 15px;
            margin-bottom: 30px;
        }
 
    }
    &_txt{
        .txt{
            display: inline-block;
            flex-shrink: 0;
            @include g.mq('md') {
                min-width: g.vw(80);
            }
            @include g.mq('xxl') {
                min-width: 80px;
            }
        }
    }
    .c_btn_link {
        margin: g.vw(40,375) auto 0;
        @include g.mq('md') {
            margin: g.vw(55) auto 0;
        }
        @include g.mq('xxl') {
            margin: 55px auto 0;
        }
    }
}

